.footer {
    width: calc(100% - 200px*2);
    padding: 66px 0 60px;
}

@media screen and (max-width: 1279px) {
    .footer {
        width: calc(100% - 70px*2);
    }
}

@media screen and (max-width: 1023px) {
    .footer {
        width: calc(100% - 19px*2);
        padding: 48px 0 36px;
    }
}